<template>
  <div class="hello">
    <div class="content">
      <div class="title">捷依问卷调查</div>
      <div class="list">
        <div class="question">1:监控系统是否方便操作？（单选）</div>
        <div class="answer">
          <div>
            <el-radio v-model="radio1" label="简单易操作">简单易操作</el-radio>
          </div>
          <div>
            <el-radio v-model="radio1" label="基本可以操作">基本可以操作</el-radio>
          </div>
          <div>
            <el-radio v-model="radio1" label="稍有难度">稍有难度</el-radio>
          </div>
          <div>
            <el-radio v-model="radio1" label="复杂繁琐">复杂繁琐</el-radio>
          </div>
          <div>
            <el-radio v-model="radio1" label="1">
              其他建议&nbsp;
              <input type="text" v-model="radio1_1" @click="btnradio1">
            </el-radio>
          </div>
        </div>
        <div class="question">2:售后服务是否及时跟进回复？（单选）</div>
        <div class="answer">
          <div>
            <el-radio v-model="radio2" label="响应速度快">响应速度快</el-radio>
          </div>
          <div>
            <el-radio v-model="radio2" label="响应速度一般">响应速度一般</el-radio>
          </div>
          <div>
            <el-radio v-model="radio2" label="响应速度慢，且不处理不回复
          ">响应速度慢，且不处理不回复</el-radio>
          </div>
        </div>
        <div class="question">3:您对产品最在意的是（单选）</div>
        <div class="answer">
          <div>
            <el-radio v-model="radio3" label="离线问题">离线问题</el-radio>
          </div>
          <div>
            <el-radio v-model="radio3" label="历史轨迹问题（不定位）">历史轨迹问题（不定位）</el-radio>
          </div>
          <div>
            <el-radio v-model="radio3" label="温度精准度问题">温度精准度问题</el-radio>
          </div>
          <div>
            <el-radio v-model="radio3" label="便携电量续航问题">便携电量续航问题</el-radio>
          </div>
        </div>
        <div class="question">4:最希望加到手机端的功能是？（单选）</div>
        <div class="answer">
          <div>
            <el-radio v-model="radio4" label="报警设置（温度、超速报警等）">报警设置（温度、超速报警等）</el-radio>
          </div>
          <div>
            <el-radio v-model="radio4" label="子用户管理">子用户管理</el-radio>
          </div>
          <div>
            <el-radio v-model="radio4" label="便携设备间隔设置">便携设备间隔设置</el-radio>
          </div>
          <div>
            <el-radio v-model="radio4" label="修改车牌">修改车牌</el-radio>
          </div>
          <div>
            <el-radio v-model="radio4" label="目前已经满足需求">目前已经满足需求</el-radio>
          </div>
          <div>
            <el-radio v-model="radio4" label="1">
              其他&nbsp;
              <input type="text" v-model="radio4_1" @click="btnradio4">
            </el-radio>
          </div>
        </div>
        <div class="question">5:您对我公司系统或者产品是否有更好的建议？（单选）</div>
        <div class="answer">
          <div>
            <el-radio v-model="radio5" label="1">
              欢迎您的反馈&nbsp;
              <input type="text" v-model="radio5_1" @click="btnradio5">
            </el-radio>
          </div>
          <div>
            <el-radio v-model="radio5" label="否">否</el-radio>
          </div>
        </div>
        <div class="btn">
          <div @click="submit" class="submit">提&nbsp;&nbsp;交</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import qs from "qs";
import $ from "jquery";
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      radio1: "",
      radio2: "",
      radio3: "",
      radio4: "",
      radio5: "",
      radio1_1: "",
      radio4_1: "",
      radio5_1: "",
      systemOperation: "",
      afterSale: "",
      productCare: "",
      mobileFunction: "",
      productSuggestion: "",
      userName: ""
    };
  },
  created() {
    console.log(this.$route.query.username);
    this.userName = this.$route.query.username;
    // this.userName = "1";
    console.log(!this.userName);
  },
  mounted() {
    console.log($("hello"));
  },
  methods: {
    callback(a) {
      alert(a.msg);
    },
    b() {},
    btnradio1() {
      this.radio1 = "1";
    },
    btnradio4() {
      this.radio4 = "1";
    },
    btnradio5() {
      this.radio5 = "1";
    },
    submit() {
      // this.userName=Math.random();
      var that = this;
      if (!this.userName) {
        this.$message({
          message: "请登录",
          type: "warning"
        });
        return;
      }
      if (this.radio1 == "") {
        this.$message({
          message: "第1题没有选择",
          type: "warning"
        });
        return;
      } else {
        this.systemOperation = this.radio1;
      }
      if (this.radio1 == "1") {
        if (this.radio1_1 == "") {
          this.$message({
            message: "请输入第1题反馈",
            type: "warning"
          });
          return;
        } else {
          this.systemOperation = this.radio1_1;
        }
      }
      if (this.radio2 == "") {
        this.$message({
          message: "第2题没有选择",
          type: "warning"
        });
        return;
      }
      if (this.radio3 == "") {
        this.$message({
          message: "第3题没有选择",
          type: "warning"
        });
        return;
      }
      if (this.radio4 == "") {
        this.$message({
          message: "第4题没有选择",
          type: "warning"
        });
        return;
      } else {
        this.mobileFunction = this.radio4;
      }
      if (this.radio4 == "1") {
        if (this.radio4_1 == "") {
          this.$message({
            message: "请输入第4题反馈",
            type: "warning"
          });
          return;
        } else {
          this.mobileFunction = this.radio4_1;
        }
      }
      if (this.radio5 == "") {
        this.$message({
          message: "第5题没有选择",
          type: "warning"
        });
        return;
      } else {
        this.productSuggestion = this.radio5;
      }
      if (this.radio5 == "1") {
        if (this.radio5_1 == "") {
          this.$message({
            message: "请输入第5题反馈",
            type: "warning"
          });
          return;
        } else {
          this.productSuggestion = this.radio5_1;
        }
      }
      console.log(
        this.systemOperation,
        this.radio2,
        this.radio3,
        this.mobileFunction,
        this.productSuggestion
      );
      var myDate = new Date();
      var y = myDate.getFullYear(); //获取完整的年份(4位,1970-????)
      var m = myDate.getMonth(); //获取当前月份(0-11,0代表1月)
      var d = myDate.getDate(); //获取当前日(1-31)
      if (d < 10) {
        d = "0" + d;
      }
      var h = myDate.getHours(); //获取当前小时数(0-23)
      if (h < 10) {
        h = "0" + h;
      }
      var s = myDate.getMinutes(); //获取当前分钟数(0-59)
      if (s < 10) {
        s = "0" + s;
      }
      var miao = myDate.getSeconds(); //获取当前秒数(0-59)
      if (miao < 10) {
        miao = "0" + miao;
      }
      var datatime = y + "-" + m + 1 + "-" + d + " " + h + ":" + s + ":" + miao;
      function callback(a) {
        alert(a.msg);
      }
      $.ajax({
        url: "https://www.123gps.com.cn/webgis/rest/userquestionnaireinfo/save",
        data: {
          userName: that.userName,
          systemOperation: that.systemOperation,
          afterSale: that.radio2,
          productCare: that.radio3,
          mobileFunction: that.mobileFunction,
          productSuggestion: that.productSuggestion,
          createTime: datatime
        },
        async: false,
        dataType: "jsonp",
        jsonp: "callback",
        jsonpCallback: "callback",
        type: "get",
        success: function(res) {
          console.log(res);
          that.$message({
            message: res.msg,
            type: "success"
          });
        },
        error: function(msg) {
          console.log(msg);
          that.$message({
            message: msg.msg,
            type: "warning"
          });
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
#app1 .el-radio__label {
  font-size: 22px;
  margin: 20px 0;
}
#app1 .el-radio {
  line-height: 2;
}
</style>

<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.hello {
  display: flex;
  justify-content: center;
  background: #fff;
}
.content {
  background: #fff;
  width: 800px;
  padding: 20px;
  padding-left: 40px;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  margin-top: 10px;
}
.title {
  /* text-align: center; */
  margin: 20px 0;
  font-size: 28px;
}
.list {
  text-align: left;
}
.question {
  margin: 20px 0;
  font-size: 24px;
}
.answer {
  margin-left: 10px;
}
.el-radio {
  color: #888;
}
.answer > div {
  margin: 10px;
}
.btn {
  display: flex;
  justify-content: center;
}
.submit {
  padding: 10px 20px;
  box-sizing: border-box;
  border-radius: 4px;
  color: #fff;
  background: #0e8eef;
  cursor: pointer;
}
input {
  cursor: pointer;
}
</style>



